import { StyledHeader } from './styled';

function Home() {
  function leadingZeroes(num) {
    if (num < 10) {
      return `00${num}`;
    } else if (num < 100) {
      return `0${num}`;
    } else {
      return num;
    }
  }

  function dateConverter(ms) {
    const msInYear = 31557600000;
    const msToM3 = msInYear * 30;
    const msSinceM3 = ms - msToM3;
    const years = Math.floor(msSinceM3 / msInYear);
    const remainder = msSinceM3 % msInYear;
    const sections = Math.floor(remainder / (msInYear / 1000));

    return `6 ${leadingZeroes(sections)} ${leadingZeroes(years)}.M42`;
  }

  return (
    <>
      <StyledHeader>The Caldraeus Campaign</StyledHeader>
      <p>
        The Caldraeus Campaign is a campaign set in the Caldraeus Sector of the Ultima Segmentum in the Era Indomitus. 
        Located in the Imperium Nihilus far from the light of the Astronomicon, the delicate balance of power that existed 
        before the Cicatrix Maledictum sundered the galaxy in half has been upended, and factions scramble to attack, defend, 
        or otherwise advance their own agendas in the ensuing chaos. What follows is a chronicle of their history so far.
      </p>
      <p style={{paddingTop: '16px'}}>
        According to local Adeptus Administratum scribes, the current date is approximately <strong>{dateConverter(Date.now())}</strong>, 
        not accounting for any distortions from the Warp or other influences but accounting for some drift since losing 
        contact with the Segmentum Solar. This date can be checked against the timeline contained herein to sense the 
        distance or proximity of the recorded events.
      </p>
    </>
  );
}

export default Home;