import { NavLink, Switch, Route, useHistory } from 'react-router-dom';

import Home from './Home';
import Background from './Background';
import Maps from './Maps';
import Events from './Events';
import Factions from './Factions';
import Characters from './Characters';

import '../styles/App.css';

function App() {
  const history = useHistory();

  return (
    <div className="App">

      <nav className="menu-nav">
        <ul id="navigation" className="font-face-ca">
          <li>
            <NavLink activeClassName="menu-active" to="/background">Background</NavLink>
          </li>
          <li>
            <NavLink activeClassName="menu-active" to="/maps">Maps</NavLink>
          </li>
          <li>
            <NavLink activeClassName="menu-active" to="/events">Events</NavLink>
          </li>
          <li>
            <NavLink activeClassName="menu-active" to="/factions">Factions</NavLink>
          </li>
          <li>
            <NavLink activeClassName="menu-active" to="/characters">Characters</NavLink>
          </li>
        </ul>
      </nav>

      <Route exact path="/">
        <div id="banner-image" onClick={() => history.push("/")}></div>
      </Route>

      <article>
        <Switch>
          <Route exact path ="/">
            <Home />
          </Route>
          <Route path="/background">
            <Background />
          </Route>
          <Route path="/maps">
            <Maps />
          </Route>
          <Route path="/events">
            <Events />
          </Route>
          <Route path="/factions">
            <Factions />
          </Route>
          <Route path="/Characters">
            <Characters />
          </Route>
        </Switch>
      </article>
    </div>
  );
}

export default App;
